import { useEffect, useCallback, useState, useContext } from "react";
import { ActivityIndicator, StyleSheet, View, ScrollView } from "react-native";
import { SafeAreaView } from "react-native-safe-area-context";

import Loading from "./Loading";
import LoadingError from "./LoadingError";

import Search from "./Search";
import Content from "./Content";

const DATA_URL = "https://flygomel.github.io/market-test-public/data.json";

export const DataContext = React.createContext();

export default function Home() {
  const [error, setError] = useState();
  const [loading, setLoading] = useState(false);
  const defaultValue = useContext(DataContext);
  const [data, setData] = useState(defaultValue);

  const load = useCallback(() => {
    setError("");
    setLoading(true);
    fetch(DATA_URL)
      .then((data) => data.json())
      .then((data) => setData(data))
      .catch((e) => setError(e.toString()))
      .finally(() => setLoading(false));
  }, []);

  useEffect(() => {
    !defaultValue && load();
  }, []);

  return (
    <SafeAreaView style={styles.container} edges={["top"]}>
      <Search />
      <ScrollView
        contentContainerStyle={[styles.scroll, loading && styles.scrollLoading]}
      >
        {error ? (
          <LoadingError error={error} onTryAgain={load} />
        ) : loading || !data ? (
          <Loading />
        ) : (
          <Content data={data} />
        )}
      </ScrollView>
    </SafeAreaView>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
  },
  scroll: {
    justifyContent: "center",
  },
  scrollLoading: {
    flexGrow: 1,
  },
});
