import { useWindowDimensions } from "react-native";

import { maxWidth } from "../../config";
import Item from "./Item";

export default function BannersBottom({ items }) {
	const { width } = useWindowDimensions();
	const padding = 10 * 2;

	const ratio = 104 / 310;
	let imageWidth = width - padding;
	if (imageWidth > maxWidth - padding) imageWidth = maxWidth - padding;
	const size = {
		width: imageWidth,
		height: imageWidth * ratio,
	};

	return items.map((item) => <Item key={item.id} {...item} size={size} />);
}
