import { useState, useCallback } from "react";
import { StyleSheet, View, FlatList, useWindowDimensions } from "react-native";

import { maxWidth } from "../../config";
import Item from "./Item";
import NavItem from "./NavItem";

export default function BannersTop({ items }) {
  const [page, setPage] = useState(0);
  const { width } = useWindowDimensions();

  const padding = 10 * 2;
  const ratio = 143 / 300;
  let imageWidth = width - padding;
  if (imageWidth > maxWidth - padding) imageWidth = maxWidth - padding;
  const size = {
    width: imageWidth,
    height: imageWidth * ratio,
  };

  const onPageSelected = useCallback(
    ({ nativeEvent }) => setPage(nativeEvent.position),
    []
  );

  const renderItem = useCallback(
    ({ item }) => {
      return <Item item={item} size={size} />;
    },
    [size]
  );

  const onScroll = useCallback(
    (e) => {
      let page = Math.min(
        Math.max(Math.floor(e.nativeEvent.contentOffset.x / width + 0.5), 0),
        items.length
      );
      if (page < 0) page = 0;
      setPage(page);
    },
    [items]
  );

  return (
    <View style={styles.container}>
      <FlatList
        style={{ height: size.height }}
        showsHorizontalScrollIndicator={false}
        removeClippedSubviews={false}
        horizontal
        pagingEnabled
        data={items}
        renderItem={renderItem}
        onScroll={onScroll}
      />
      <View style={styles.nav}>
        {items.map((_, index) => (
          <NavItem selected={index === page} key={_.id} />
        ))}
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {},
  nav: {
    flexDirection: "row",
    justifyContent: "center",
    paddingVertical: 10,
  },
});
