import { StyleSheet, View } from "react-native";

export default function NavItem({ selected }) {
	return (
		<View
			style={[styles.container, selected && styles.selected]}
			selected={selected}
		/>
	);
}

const styles = StyleSheet.create({
	container: {
		width: 10,
		height: 10,
		marginHorizontal: 3,
		borderRadius: 5,
		borderWidth: 1,
		borderColor: "#9D9D9D",
	},
	selected: {
		backgroundColor: "#FFE40D",
		borderColor: "#00B956",
	},
});
